  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px; }

.title {
  margin-top: 0;
  font-weight: normal;
  font-size: 36px;
  font-family: "Roboto Slab" ,sans-serif;
  line-height: 1.333;
  text-align: center;
