  color: #fff;
  background-color: #33c7fd;
  transition: 0.2s; }
  .button:hover {
    background-color: #13b0e9; }
  .button:active {
    background-color: #64d6ff; }

.phone-number {
  display: block;
  font-weight: bold;
  font-size: 14px;
  font-family: "Raleway";
  color: #0d3351;
  transition: 0.15s; }
  .phone-number:hover {
    color: #13b0e9; }
  .phone-number:active {
    color: #64d6ff; }

.phone-label {
  display: block;
  font-size: 12px;
  color: #0d3351; }

.header {
  background: url("../img/backgrounds/header-bg.png") no-repeat 50% 50%;
  background-size: cover; }
  .header__container {
    padding-bottom: 195px;
    position: relative; }
  .header__logo {
    width: 88px;
    height: 88px;
    margin: 3px auto; }
    .header__logo img {
      width: 100%;
      height: 100%; }
  .header__title {
    margin-bottom: 16px; }
  .header__text {
    display: block;
    margin-bottom: 32px;
    font-size: 100px;
    font-family: "Roboto Slab", sans-serif;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: -2.5px;
    color: #0d3351; }
  .header__dotted-line {
    position: absolute;
    bottom: -95px;
    left: calc(50% - 8px);
    height: 275px;
    transform: translateX(-50%);
    overflow: hidden; }

.navigation {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 84px; }
  .navigation__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -5px;
    padding: 5px;
    background: none;
    border: none;
    cursor: pointer; }
    .navigation__button.navigation__button_active .navigation__burger-element {
      opacity: 0;
      width: 0; }
    .navigation__button.navigation__button_active .navigation__burger:before {
      transform: rotate(45deg);
      top: -1px; }
    .navigation__button.navigation__button_active .navigation__burger:after {
      transform: rotate(-45deg);
      bottom: -1x; }
  .navigation__burger {
    position: relative;
    width: 21px;
    height: 16px; }
    .navigation__burger:before, .navigation__burger:after, .navigation__burger-element {
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #0d3351;
      transform-origin: left center;
      transition: 0.3s; }
    .navigation__burger:before, .navigation__burger:after {
      display: block;
      content: ''; }
    .navigation__burger:before {
      top: 0;
      transform: rotate(0); }
    .navigation__burger:after {
      bottom: 0;
      transform: rotate(0); }
    .navigation__burger-element {
      top: calc(50% - 1px); }
  .navigation__group {
    margin-left: auto;
    display: flex; }
  .navigation__item {
    margin-left: 35px;
    padding-top: 5px; }
    .navigation__item:not(:last-child) {
      cursor: pointer; }
    .navigation__item img {
      display: block;
      width: 10px;
      height: 12px;
      margin-bottom: 2px; }
    .navigation__item:not(:last-child) {
      display: flex;
      align-items: center; }
      .navigation__item:not(:last-child):after {
        display: block;
        content: '';
        border-top: 5px solid #0d3351;
        border-bottom: 5px solid rgba(0, 0, 0, 0);
        border-left: 4px solid rgba(0, 0, 0, 0);
        border-right: 4px solid rgba(0, 0, 0, 0);
        transform: translateY(25%); }
    .navigation__item-text {
      margin: 0 8px;
      font-size: 15px; }

.menu {
  position: absolute;
  left: 0;
  top: 60px;
  z-index: 10;
  width: 0;
  background-color: #0d3351;
  opacity: 0;
  transition: width 0.3s, opacity 0.5s;
  overflow: hidden; }
  .menu.menu_visible {
    width: 420px;
    opacity: 1; }
  .menu__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 64px 45px 54px; }
  .menu__item {
    margin-bottom: 15px; }
  .menu__reference {
    display: block;
    padding: 5px 0;
    font-size: 18px;
    font-family: "Roboto Slab", sans-serif;
    color: #fff;
    letter-spacing: 0.8px;
    white-space: nowrap; }

.main-content {
  background-color: #f3f3f3; }
  .main-content__container {
    padding-top: 107px;
    padding-bottom: 63px; }
  .main-content__title {
    margin-bottom: 7px; }
  .main-content__sort {
    display: block;
    text-align: center;
    letter-spacing: 1px;
    color: #0d3351; }
    .main-content__sort-item {
      position: relative;
      display: inline-flex;
      font-style: italic; }
      .main-content__sort-item:after {
        position: absolute;
        right: -16px;
        top: 5px;
        display: block;
        content: '';
        border-top: 5px solid #0d3351;
        border-bottom: 5px solid rgba(0, 0, 0, 0);
        border-left: 4px solid rgba(0, 0, 0, 0);
        border-right: 4px solid rgba(0, 0, 0, 0);
        transform: translateY(25%); }
  .main-content__more-cruises {
    border: none;
    letter-spacing: 0.8px;
    cursor: pointer; }
    .main-content__more-cruises:after {
      position: relative;
      top: 1px;
      left: 10px;
      display: block;
      content: '';
      border-top: 5px solid #fff;
      border-bottom: 5px solid rgba(0, 0, 0, 0);
      border-left: 4px solid rgba(0, 0, 0, 0);
      border-right: 4px solid rgba(0, 0, 0, 0);
      transform: translateY(25%); }

.catalog {
  padding: 42px 0 5px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  display: block; }
  .catalog:after {
    display: table;
    content: '';
    clear: both; }
  .catalog__item {
    float: left;
    width: calc(100% / 12 * 6 - 15px * 2);
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 20px; }

.cruise {
  position: relative;
  background-color: #fff; }
  .cruise__special {
    position: absolute;
    right: 30px;
    top: -10px;
    display: none;
    justify-content: center;
    align-items: center;
    width: 90px;
    height: 82px;
    color: #fff; }
    .cruise__special:after {
      position: absolute;
      bottom: 0;
      display: block;
      content: '';
      border-top: none;
      border-bottom: 20px solid rgba(0, 0, 0, 0);
      border-left: 45px solid rgba(0, 0, 0, 0);
      border-right: 45px solid rgba(0, 0, 0, 0);
      transform: translateY(100%); }
    .cruise__special_hit {
      display: flex;
      background-color: #33c7fd; }
      .cruise__special_hit:after {
        border-left-color: #33c7fd;
        border-right-color: #33c7fd; }
    .cruise__special_sale {
      display: flex;
      background-color: #f20542; }
      .cruise__special_sale:after {
        border-left-color: #f20542;
        border-right-color: #f20542; }
  .cruise__image img {
    display: block; }
  .cruise__description {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 24px 38px 37px; }
  .cruise__reference {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 18px;
    font-family: "Roboto Slab", sans-serif;
    letter-spacing: 0.8px;
    color: #00a2dd;
    transition: 0.15s; }
    .cruise__reference:hover:after {
      transform: translatex(5px); }
    .cruise__reference:active {
      color: #64d6ff; }
      .cruise__reference:active:after {
        border-left-color: #64d6ff; }
    .cruise__reference:after {
      margin-top: 2px;
      margin-left: 12px;
      display: block;
      content: '';
      border-top: 4px solid rgba(0, 0, 0, 0);
      border-bottom: 4px solid rgba(0, 0, 0, 0);
      border-left: 5px solid #00a2dd;
      border-right: 5px solid rgba(0, 0, 0, 0);
      transition: 0.2s; }
  .cruise__route, .cruise__time {
    margin-top: 0;
    margin-bottom: 10px; }
    .cruise__route-key, .cruise__time-key {
      font-weight: bold; }
  .cruise__time {
    margin-bottom: 15px; }
  .cruise__price-actual {
    font-size: 30px;
    font-family: "Roboto Slab", sans-serif;
    letter-spacing: 1px; }
  .cruise__price-old {
    margin-left: 30px;
    font-size: 18px;
    font-family: "Roboto Slab", sans-serif;
    color: rgba(13, 51, 81, 0.549);
    text-decoration: line-through; }

.footer {
  background-color: #fff; }
  .footer__container {
    padding-top: 26px;
    padding-bottom: 30px; }
  .footer__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: 0;
    margin-right: 0; }
  .footer__item:nth-child(2) {
    flex-grow: 1; }
  .footer__menu-list {
    margin-right: 160px; }
  .footer__menu-item {
    margin-bottom: 8px; }
  .footer__menu-reference {
    color: #0d3351;
    transition: 0.15s; }
    .footer__menu-reference:hover {
      color: #13b0e9; }
    .footer__menu-reference:active {
