.footer-social__list {
  display: flex; }

.footer-social__title {
  display: block;
  margin-bottom: 10px; }

.footer-social__reference {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background-color: #0d3351;
  border-radius: 50%;
  transition: 0.15s; }
  .footer-social__reference:hover {
    background-color: #13b0e9; }
  .footer-social__reference:active {
    background-color: #64d6ff; }
  .footer-social__reference img {
