.modal-background {
  position: fixed;
  z-index: 100;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }

.modal {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin: auto;
  padding: 118px 34px 88px;
  width: 850px;
  background-color: #fff; }
  .modal__close {
    position: absolute;
    top: 17px;
    right: 28px;
    padding: 5px;
    font-weight: 500;
    font-size: 20px;
    color: #8699a8;
    transform: scaleX(1.1);
    cursor: pointer; }
  .modal__gallery {
    width: calc(100% / 12 * 6 - 15px * 2);
    margin-left: 15px;
    margin-right: 15px; }
  .modal__gallery-main {
    position: relative;
    width: 100%;
    margin-bottom: 8px;
    padding-top: 66.5%;
    overflow: hidden; }
    .modal__gallery-main img {
      position: absolute;
      top: 50%;
      left: 50%;
      max-width: 100%;
      display: block;
      transform: translate(-50%, -50%); }
  .modal__gallery-container {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px; }
  .modal__gallery-item {
    width: calc(100% / 12 * 3 - 4px * 2);
    margin-left: 4px;
    margin-right: 4px;
    overflow: hidden; }
  .modal__gallery-image {
    position: relative;
    padding-top: calc(66.2%); }
    .modal__gallery-image img {
      position: absolute;
      top: 50%;
      left: 50%;
      max-width: 100%;
      display: block;
      transform: translate(-50%, -50%); }
  .modal__info {
    width: calc(100% / 12 * 6 - 15px * 2);
    margin-left: 15px;
    margin-right: 15px; }
  .modal__cruise {
    display: block;
    margin-top: -3px;
    margin-bottom: 38px;
    font-size: 18px;
    font-family: "Roboto Slab", sans-serif;
    line-height: 1;
    letter-spacing: 0.8px;
    color: #00a2dd; }
  .modal__route {
    margin-bottom: 8px; }
  .modal__schedule {
    padding-bottom: 15px; }
  .modal__schedule-title {
    display: block;
    margin-bottom: 5px;
    font-weight: bold; }
  .modal__schedule-container p {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1.8; }
  .modal__price {
    margin-bottom: 15px; }
  .modal__reference {
    margin: 0; }

body.modal_open {
  position: fixed;
  overflow: hidden;
  width: 100%;
  height: 100%; }
